<template>
  <header id="header" class="sticky-top main-header">
    <!-- Navbar -->
    <nav class="primary-menu navbar navbar-expand-lg navbar-dark  border-bottom-0">
      <div class="container position-relative   ps-3 px-lg-3 pt-lg-3 pb-lg-2">

        <!-- Logo -->
        <a class="navbar-brand" href="/">
          <img src="logo.ico" width="45" height="45">
          <h6 class="d-inline-block ms-2 me-2 fw-bold">Notello <span >Rolls</span></h6>

        </a>
        <!-- Logo End -->
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#header-nav">
          <i class="fas fa-bars"></i>
        </button>

        <div id="header-nav" class="collapse navbar-collapse w-100 my-lg-auto">
          <ul class="navbar-nav ms-auto me-auto mb-2 mb-lg-0">
            <li class="nav-item">
              <a class="nav-link smooth-scroll active" href="#home">{{ $t("home") }}</a></li>
            <li class="nav-item"><a class="nav-link smooth-scroll" href="#products">{{ $t("products") }}</a></li>
            <li class="nav-item"><a class="nav-link smooth-scroll" href="#contact">{{ $t("contact") }}</a></li>
          </ul>
          <ul class="navbar-nav  mb-2 mb-lg-0">
            <li class="nav-item dropdown  dropdown-hover">
              <a class="nav-link" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                <img :src="'lang/'+this.lang+'.svg'" class="d-inline ms-1 me-1" width="25" height="25">
                <span>{{$t('language')}}</span>
              </a>
              <ul class="dropdown-menu">
                <li>
                  <a @click="changeLanguage('en')" class="dropdown-item" href="#">
                    <img class="rounded-circle me-1 ms-1" src="lang/en.svg" width="25" height="25">{{$t('english')}}</a>
                </li>
                <li>
                  <a @click="changeLanguage('ar')" class="dropdown-item" href="#">
                    <img class="rounded-circle me-1 ms-1" src="lang/ar.svg" width="25" height="25">{{$t('arabic')}}</a>
                </li>
                <li>
                  <a @click="changeLanguage('swe')" class="dropdown-item" href="#">
                    <img class="rounded-circle me-1 ms-1" src="lang/swe.svg" width="25" height="25">{{$t('sweden')}}</a>
                </li>
              </ul>
            </li>


          </ul>
        </div>


      </div>
    </nav>
    <!-- Navbar End -->
  </header>
</template>

<script>


export default {
  name: "HeaderApp",
  data(){
    return{
      checked:true,
      isScrolled:false,
      lang:"en"
    };

  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
    if(this.$i18n.locale.includes("en")===true){
      this.lang="en"
    }else if(this.$i18n.locale.includes("swe")===true){
      this.lang="swe"
    }else if(this.$i18n.locale.includes("ar")===true){
      this.lang="ar"
    }else {
      this.lang="en"
    }

  },
  methods:{
  handleScroll () {
    if(window.scrollY > 10){
      window.$('.main-header').addClass('sticky slideInDown');
    }else {
      window.$('.main-header').removeClass('sticky slideInDown');
    }
  },
  changeLanguage(lang){

    localStorage.setItem('lang',lang);
    this.$i18n.locale=lang;
    this.lang=lang;
    document.documentElement.setAttribute("lang", lang);
    window.location.reload();
    /* if(this.$i18n.locale==='ar'){
       document.documentElement.setAttribute("lang", localStorage.getItem('lang'));
       document.documentElement.setAttribute("dir", 'rtl');

     }else{
       document.documentElement.setAttribute("lang", this.$i18n.locale);
       document.documentElement.setAttribute("dir", 'ltr');
     }*/
  },


}

}
</script>

