<template>
  <swiper
      dir="auto"
      :spaceBetween="0"
      :centeredSlides="true"
      :speed="2000"
      :loop="true"
      :autoplay="{
      delay: 5000,


      disableOnInteraction: false,
    }"
      :pagination="{
      clickable: true,
    }"
      :navigation="true"
      :modules="modules"
      class="mySwiper"

  >
    <swiper-slide><img src="images/slider/s1.jfif"></swiper-slide>
    <swiper-slide><img src="images/slider/s2.jfif"></swiper-slide>
    <swiper-slide><img src="images/slider/s3.jfif"></swiper-slide>
    <swiper-slide><img src="images/slider/s4.jpg"></swiper-slide>
    <swiper-slide><img src="images/slider/s5.jpg"></swiper-slide>
    <swiper-slide><img src="images/slider/s6.jpg"></swiper-slide>

  </swiper>
</template>

<script>
// Import Swiper Vue.js components
import { Swiper,SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/pagination";
import "swiper/css/navigation";

// import required modules
import { Autoplay, Pagination, Navigation } from "swiper";



export default {
  name: "SwiperAutoplay",
  components: {

    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [Autoplay, Pagination, Navigation],
    };
  },
}
</script>

<style scoped>
img{
  width: 100vw;
  min-height: 100vh;
  max-height: 100vh;
}

</style>