<template>
  <div    :class="'col-sm-6 col-lg-4 artwork ' +this.category" style="position: absolute; left: 0px; top: 0px;">
    <div class="portfolio-box rounded">
      <div class="portfolio-img rounded">
        <img class="img-fluid d-block" :src="this.img" alt="">
        <div class="portfolio-overlay">
          <a :class="this.mySelector +' stretched-link'"
            @click="openGallery"
             data-desc-position="right"
             data-effect="fade"
             data-width="900px"
             data-height="auto"
             data-zoomable="true"
             data-draggable="true"
          ></a>

          <div class="portfolio-overlay-details">
            <h5 class="text-white fw-400">{{ this.title }}</h5>
            <span class="text-light">{{ this.description }}</span> </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import  "glightbox/dist/css/glightbox.css";
import  "glightbox/dist/js/glightbox.js";
import GLightbox from 'glightbox';
export default {
  name: "PortfolioBox",

  props:{
    category:String,
    img:String,
    title:String,
    description:String,
    elements:Array,
    mySelector:String,
    myLoop:Boolean,
    autoPlayVideos:Boolean,
    width:Number

  },
  data(){
    return{
      myGallery:{}
    }
  },
  mounted() {
    //lightbox settings
     this.myGallery = new GLightbox({
      selector: '.'+this.mySelector,
      'touchNavigation': true,
      loop: this.loop,
      elements:this.elements,
      'width': 900,
      'autoPlayVideos': this.autoPlayVideos,

     openEffect: 'zoom',
      closeEffect: 'fade',

    });



  },
  methods:{
    openGallery(){
      this.myGallery.open();
    }
  }


}
</script>

<style scoped>
.stretched-link{
  cursor: pointer;
}
</style>